<template>
  <div class="submit-form">
    <div v-if="!stat.submitted">
      <div class="form-group">
        <h1 class="my-5">Agregar al stock</h1>
        <label for="Nombre">Nombre</label>
        <input
          type="text"
          class="form-control"
          id="nombre"
          required
          v-model="formItem.nombre"
        />
      </div>

      <div class="form-group">
        <label for="Precio">Precio</label>
        <input
          type="number"
          class="form-control"
          id="precio"
          required
          v-model="formItem.precio"
        />
      </div>

      <div class="form-group">
        <label for="Nombre">Cantidad</label>
        <input
          type="text"
          class="form-control"
          id="cantidad"
          required
          v-model="formItem.cantidad"
        />
      </div>

      <div>
        <button @click="onSubmitItem" class="btn btn-success my-5 w-100">
          Agregar
        </button>
      </div>
    </div>

    <div class="d-flex my-5 justify-content-center" v-else>
      <div class="d-flex flex-column my-5">
        <h1>Guardado en stock</h1>
        <button @click="nuevoItem" class="btn btn-success my-5">
          Agregar nuevo item
        </button>
      </div>
    </div>
  </div>

  <div class="submit-form">
    <div>
      <div class="form-group">
        <h1 class="my-5">Agregar cliente</h1>
        <label for="Nombre">Nombre</label>
        <input
          type="text"
          class="form-control"
          id="nombre"
          required
          v-model="formCliente.nombre"
        />
      </div>
      <div> 
        <button @click="onSubmitCliente" class="btn btn-success my-5 w-100">
          Agregar
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { createItem, createCliente } from "@/firebase";
import { reactive } from "vue";

export default {
  setup() {
    const formItem = reactive({ nombre: "", precio: null, cantidad: null });
    const formCliente = reactive({ nombre: "", saldo: 0})
    const stat = reactive({ submitted: false });

    const onSubmitItem = async () => {
      formItem.cantidad = Number(formItem.cantidad)
      await createItem({ ...formItem });
      formItem.nombre = "";
      formItem.precio = null;
      formItem.cantidad = null;
      stat.submitted = true;
    };

    const onSubmitCliente = async () => {
      await createCliente({ ...formCliente });
      formCliente.nombre = "";
      formCliente.saldo = 0;
      stat.submitted = true;
    };

    return { formItem, formCliente, onSubmitItem, onSubmitCliente, stat };
  },
  methods: {
    nuevoItem() {
      this.stat.submitted = false;
    },
  },
};
</script>

<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}

.btn-success {
  background: #4dba87;
}
</style>